class Spiral {
  constructor(app, options) {
    this.app = app;
    this.options = options;
  }
  init() {
    // var scene = new THREE.Scene();
    // var camera = new THREE.PerspectiveCamera(60, window.innerWidth / window.innerHeight, 1, 1000);
    // camera.position.set(0, 30, 30);

    let scene = this.app.scene;
    // scene.add(new THREE.GridHelper(20, 10));

    var radius = 10;
    var turns = 10;
    var objPerTurn = 30;

    var angleStep = (Math.PI * 2) / objPerTurn;
    var heightStep = 0.5;

    var geom = new THREE.BoxBufferGeometry(2, 3, 1);

    const light = new THREE.PointLight( 0xffffff,1,200 );
    light.position.set(0,0,-20);
    this.app.camera.add( light );

    let bulb = new Mesh(
      new THREE.SphereBufferGeometry( .2, 32,32 ),
      new THREE.MeshBasicMaterial( { color: 0xffffff } )
    );
    light.add(bulb);

    // scene.add(new THREE.PointLightHelper(light));

    scene.add(new THREE.AmbientLight(0xffffff, 0.2));
    // scene.add(new THREE.PointLight(0xffffff, 0.5));


    this.group = new THREE.Group();

    for (let i = 0; i < turns * objPerTurn; i++) {
      let plane = new THREE.Mesh(
        geom,
        new THREE.MeshLambertMaterial({
          color: this.options.color,
        })
      );

      // position
      plane.position.set(Math.cos(angleStep * i) * radius, heightStep * i, Math.sin(angleStep * i) * radius);

      // rotation
      this.group.add(plane);
      plane.rotation.y = -angleStep * i;
      // scene.add(plane);
    }

    scene.add(this.group);


  }

  update(time) {

    if(this.options.rotateBlock) {
      this.group.children.forEach((child) => {
        child.rotation.y += this.options.rotateBlock;
        // console.log(child);
      });
    }

    if(this.options.rotateCorck) {
      this.group.rotation.y += this.options.rotateCorck;

    }

    // this.group.rotation.y += 0.01;
    // this.group.rotateX(10);
    this.group.children.forEach(function(child){
      // child.rotation.y += .01;
      // console.log(child);
    });
  }
}

const allParticles = [];
function particles(scene) {
  var geometry = new THREE.BufferGeometry();
  var vertices = [];
  var materials = [], parameters;
  for ( var i = 0; i < 550; i ++ ) {
    var x = Math.random() * 60 - 30;
    var y = Math.random() * 60 - 30;
    var z = Math.random() * 60 - 30;
    vertices.push( x, y, z );
  }
  geometry.setAttribute( 'position', new THREE.Float32BufferAttribute( vertices, 3 ) );
  parameters = [
    [[ 0.3, 0.7, 0.9 ], texBlur, .2 ],
    [[ 0.3, 0.3, 0.8 ], texBlur2, .4 ]
  ];
  for ( var i = 0; i < parameters.length; i ++ ) {
    var color = parameters[ i ][ 0 ];
    var sprite = parameters[ i ][ 1 ];
    var size = parameters[ i ][ 2 ];
    materials[ i ] = new THREE.PointsMaterial( { size: size, map: sprite, blending: THREE.AdditiveBlending, depthTest: false, transparent: true, opacity:.35} );
    materials[ i ].color.setRGB( color[ 0 ], color[ 1 ], color[ 2 ] );
    var particles = new THREE.Points( geometry, materials[ i ] );
    particles.rotation.x = Math.random() * 6;
    particles.rotation.y = Math.random() * 6;
    particles.rotation.z = Math.random() * 6;
    
    allParticles.push(particles);
    scene.add( particles );

    return particles;
  }
}

